/* imports
--------------------------------*/
@import 'colors';

/* SASS Variables
--------------------------------*/
$border: solid 1px;
$body-font-size: 10px;

/* SASS Extends
--------------------------------*/
%sections{
    height: 100rem;
    max-height: 100%;
}


/* START STYLING INDEX PAGE
-----------------------------------------------------------------------------*/
html {
    font-size: $body-font-size;
    font-family: 'Barlow', sans-serif;
}
* {
    margin: 0;
    padding: 0;
}
.main-body {
    border: $border $red-color;
    padding-right: 0rem !important;
    padding-left: 0rem !important;
    height: 100%;
}
header.main-header {
    background-color: transparent;
    position: fixed;
    top: 0rem;
    border: $border green;
    z-index: 2;
    padding: 0rem;
}
div.site-logo {
    img {
        width: 6rem;
    }
}
li.styled-active{

    a{
        color: $red-color !important;
    }
}
nav.main-nav {
    ul {
        float: right;
        background-color: transparent;
        li {
            a {
                font-size: 2.5rem !important;
                padding-right: 2rem;
                color: $gray-color;
                height: 100%;
                font-family: 'Barlow Condensed', sans-serif;
                font-weight: bold;
                &:hover, &:focus{
                    color: $red-color;
                }
            }
        }
    }
}
section.hero-img {
    background: url(../imgs/white-bg.jpg) no-repeat;
    background-size: cover;
    background-position: center bottom;
    height: 100rem;
    max-height: 100%;
    position: fixed;
    z-index: -1;
    top: 0em;
    left: 0em;
    overflow: hidden;
    border: $border yellow;
}
section.first-section {
    @extend %sections;
}
div.intro-text {
    height: 20rem;
    position: fixed;
    top: 30rem;
    z-index: -1;
    h1 {
        width: 100%;
        color: $red-color;
        font-size: 20rem;
        text-align: center;
        margin: 0 auto;
        font-weight: 600;
        line-height: 17rem;
        font-family: 'Barlow Condensed', sans-serif;
        text-transform: uppercase;
    }
    h2 {
        width: 100%;
        color: $gray-color;
        font-size: 5rem;
        text-align: center;
        margin: 0 auto;
        font-weight: bold;
        font-family: 'Barlow Condensed', sans-serif;
    }
}

