section.second-section {
    @extend %sections;
    background-color: $red-color;
}
// This is to prevent FOUC (flash of unstyled content.)
.no-js .top-bar {
    display: none;
}
/* Small screen only */
@media screen and (max-width: 39.9375em) {
    // This is to prevent FOUC (flash of unstyled content.)
    .no-js .top-bar {
        display: block;
    }
    .no-js .title-bar {
        display: none;
    }
    .site-logo {
        img {
            width: 6rem;
        }
    }
    div.button-container {
        padding: 0.9rem;
        background-color: transparent;
    }
    .menu-tag {
        width: 4rem;
        height: 2.7rem;
    }
    .menu-tag::after {
        width: 4rem;
        background: $red-color;
        box-shadow: 0 1rem 0 $red-color, 0 2rem 0 $red-color;
        height: 0.5rem;
        position: absolute;
        top: 0.5rem;
        left: -0.1rem;
        display: block;
    }
    .menu-icon:hover::after {
        background: $gray-color;
        box-shadow: 0 1rem 0 $gray-color, 0 2rem 0 $gray-color;
    }
    nav.main-nav {
        width: 100% !important;
        margin-top: 0.5rem;
        ul {
            width: 100% !important;
            background-color: $white-color;
            margin-bottom: 1rem;
            li {
                width: 100% !important;
                font-weight: bolder;
                font-size: 1.5rem !important;
                font-weight: 100;
                text-align: center;
                border-bottom: solid 0.2rem $red-color;
                &:first-child {
                    border-top: solid 0.2rem $red-color;
                }
                a {
                    padding: 2rem 1rem 2rem 1rem;
                    background-color: transparent !important;
                    box-sizing: border-box;
                }
                li.styled-active {
                    a {
                        padding-right: 0rem;
                    }
                }
            }
        }
    }
    
    div.intro-text {
        height: 35rem;
    }
}
/* Medium only */
@media screen and (min-width: 40em) and (max-width: 63.9375em) {
    // This is to prevent FOUC (flash of unstyled content.)
    .no-js .top-bar {
        display: block;
    }
    .no-js .title-bar {
        display: none;
    }
    
    div.intro-text {
        height: 35rem;
    }
}